import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setFavorite, unsetFavorite } from '../../redux/adverts/slice';
import PropTypes from 'prop-types';
import { selectFavoriteCars } from '../../redux/adverts/selectors';
import Notiflix from 'notiflix';
import MoreInfoModal from '../MoreInfoModal/MoreInfoModal';

import {
  Button,
  HeartIconButton,
  IconHeart,
  Image,
  CarCardTextWrapper,
  CarCardText,
  CarCardPrice,
  CarCardSecondText,
  Span,
  Wrapper,
} from './CarCard.styled';

const CarCard = ({ car }) => {
  const {
    id,
    img,
    description,
    make,
    model,
    year,
    rentalPrice,
    address,
    rentalCompany,
    type,
    functionalities,
  } = car;
  const dispatch = useDispatch();

  const favoriteCars = useSelector(selectFavoriteCars) || [];

  const isFavorite = favoriteCars.some(
    favoriteCar => favoriteCar.id === car.id
  );

  const handleToggleFavorite = () => {
    if (isFavorite) {
      dispatch(unsetFavorite(car));
      Notiflix.Notify.success('Remove from Favorites');
    } else {
      dispatch(setFavorite(car));
      Notiflix.Notify.success('Add to Favorites');
    }
  };

  const [, city, country] = address ? address.split(', ') : ['', ''];
  const [functional] = functionalities || [''];

  const [showModal, setShowModal] = useState(false);

  const openModal = () => {
    setShowModal(true);
  };

  const closeModal = () => {
    setShowModal(false);
  };

  return (
    <>
      <Wrapper>
        <Image src={img} alt={description} />
        <CarCardTextWrapper>
          <CarCardText>
            {make} <Span>{model}</Span>, {year}
          </CarCardText>
          <CarCardPrice>{rentalPrice}</CarCardPrice>
        </CarCardTextWrapper>

        <CarCardSecondText>
          {city} | {country} | {rentalCompany} | {type} | {make} | {id} |{' '}
          {functional}
        </CarCardSecondText>
        <Button type="button" onClick={openModal}>
          Learn more
        </Button>
        <HeartIconButton
          type="button"
          aria-label="Add to favorites"
          onClick={handleToggleFavorite}
        >
          <IconHeart data-isfavorite={isFavorite} />
        </HeartIconButton>
      </Wrapper>      
      {showModal && <MoreInfoModal onClose={closeModal} data={car} />}
    </>
  );
};

CarCard.propTypes = {
  car: PropTypes.shape({
    id: PropTypes.number.isRequired,
    description: PropTypes.string.isRequired,
    year: PropTypes.number.isRequired,
    make: PropTypes.string.isRequired,
    model: PropTypes.string.isRequired,
    type: PropTypes.string.isRequired,
    img: PropTypes.string,
    functionalities: PropTypes.array,
    rentalPrice: PropTypes.string.isRequired,
    rentalCompany: PropTypes.string.isRequired,
    address: PropTypes.string.isRequired,
    // mileage: PropTypes.number.isRequired,
  }).isRequired,
};

export default CarCard;
